import Vue from 'vue';
import App from './App.vue';
import vuetify from './plugins/vuetify';
import { ValidationObserver, ValidationProvider, extend, localize } from 'vee-validate';
import es from 'vee-validate/dist/locale/es.json';
import * as rules from 'vee-validate/dist/rules';
import { VueReCaptcha } from "vue-recaptcha-v3";
import axios from 'axios'
import VueAxios from 'vue-axios'


Vue.config.productionTip = false

// install rules and localization
Object.keys(rules).forEach(rule => {
  extend(rule, rules[rule]);
});

localize('es', es);

// Install components globally
Vue.component('ValidationObserver', ValidationObserver);
Vue.component('ValidationProvider', ValidationProvider);

Vue.use(VueReCaptcha, { siteKey: "6Lfvf_sUAAAAAL0embBQdMacKpgW_J2Bjko8yWwx" });
Vue.use(VueAxios, axios)

// Agregamos la URL base de nuestra API
const production  = 'https://pytd.herokuapp.com/api';
const development = 'http://localhost:3000/api';
axios.defaults.baseURL = (process.env.NODE_ENV ? production : development);

new Vue({
  vuetify,
  render: h => h(App)
}).$mount('#app')
