<template>
  <v-container>
    <v-row no-gutters>
      <v-col>
        <v-card elevation="0">
          <ValidationObserver ref="observer">
            <form>
              <v-col cols="12">
                <ValidationProvider v-slot="{ errors }" name="identificación" rules="required|max:10|numeric">
                  <v-text-field
                    v-model="identify"
                    :counter="10"
                    :error-messages="errors"
                    label="Identificación"
                    required
                  ></v-text-field>
                </ValidationProvider>            
              </v-col>

              <v-col cols="12">
                <ValidationProvider v-slot="{ errors }" name="orden" rules="required|numeric">
                  <v-text-field
                    v-model="order"
                    :error-messages="errors"
                    label="Orden de Servicio"
                    required
                  ></v-text-field>
                </ValidationProvider>
              </v-col>

              <!-- <v-col cols="12">
                <button @click="recaptcha">Execute recaptcha</button>            
              </v-col> -->

              <v-col cols="12">
                <v-btn class="mr-4" @click="submit">Consultar resultado</v-btn>
                <v-btn class="mr-4" @click="clear">Limpiar</v-btn>
                <v-btn v-if="preview" @click="download">Descargar</v-btn>
              </v-col>
            </form>
          </ValidationObserver>
        </v-card>

        <v-card v-show="preview" elevation="0">
          <div id="resultadoPYTD">
            <!-- <iframe src="https://drive.google.com/file/d/1DOAP7YeK2_tdyp7BWeWQuQ01OsdeNeJ7/preview" width="640" height="480"></iframe> -->
          </div>
        </v-card>
      </v-col>
    </v-row>

    <v-snackbar
      v-model="snackbar"
      color="info"
      top
    >
      {{ e_text }}
      <v-btn
        dark
        text
        @click="snackbar = false"
      >
        Cerrar
      </v-btn>
    </v-snackbar>

  </v-container>
</template>

<script>
  import PDFObject from 'pdfobject'
  export default {
    name: 'SearchResult',
    data: () => ({
      identify: '',
      order: '',
      preview: false,
      urldown: '',
      namefile: '',
      snackbar: false,
      e_text : ''
    }),
    methods: {
      submit () {
        this.$refs.observer.validate().then((valid) => {
          if (valid) {
            const url = 'results?identify=' + this.identify + '&order=' + this.order;
            this.axios.get(url)
              .then((response) => {
                // console.log('response: ', response.data.id);
                PDFObject.embed("https://drive.google.com/file/d/" + response.data.id + "/preview","#resultadoPYTD");
                this.urldown = "https://drive.google.com/u/0/uc?id=" + response.data.id + "&export=download";
                this.namefile = response.data.name;
                this.preview = true;
              })
              .catch((error)=>{
                if (error.response.status === 404) {
                  this.e_text = error.response.data.msg;
                  this.snackbar = true;
                } else {
                  console.error(error);
                }
              })
          }
        });
      },
      clear () {
        this.identify = ''
        this.order = ''
        this.preview = false
        this.urldown = ''
        this.namefile = ''
        this.$refs.observer.reset()
      },
      download () {
        const link = document.createElement('a')
        link.href = this.urldown
        link.download = this.namefile
        document.body.appendChild(link)
        link.click();
        link.remove();
      },
      async recaptcha() {
        // this.$refs.observer.validate().then((valid) => {
        //   if (valid) {
            // (optional) Wait until recaptcha has been loaded.
            await this.$recaptchaLoaded()
    
            // Execute reCAPTCHA with action "login".
            // const token = await this.$recaptcha('login')
    
            // Do stuff with the received token.
        //   }
        // });
      }
    }
  }
</script>



<style>
  .pdfobject-container { height: 60rem; width: 50rem; border: 1rem solid rgba(0,0,0,.1); }
</style>